<script>
    import { mapMutations } from "vuex";
    export default {
        data() {
			return {
				cartStatus:false
            }
        },
        created(){
            this.changePage('Home');
			this.$nextTick(()=>{this.getPushIndex()});
        },
        methods:{
            ...mapMutations(["changePage"]),
            getProjectList(){
				this.loading = true;
				const {pageSize,pageNum} = this;
				this.$post('project/getProjectUserList',{pageSize,pageNum}).then(res=>{      
					this.loading = false;
					this.projectList= res.data
					this.allprojectList = res.data
				})
            },
            getPushIndex(){
                let el = localStorage.getItem('pushIndex');
                if(el){
                    if(el == 'About'){
                        document.getElementById('comp-izq2o5jt').scrollIntoView(true);
                    }else{
                        document.getElementById('comp-izficunz').scrollIntoView(true);
                    }
                }
            }
        },
        destroyed(){
            localStorage.removeItem('pushIndex');
        }
    };
</script>

